
const Reminder = () => (
    <div>
        <section>
            <div class="container">
            <div class="row">
                <div class="col-md-9">
                    <h3>Protect your child now</h3>
                    <h1>Register for immunization reminder, <br/>track immunization and receive immunization tips</h1>
                </div>
                <div class="col-md-3" style="padding: 60px;">
                    <a href="#">
                        <button class="btn b-button-lg" data-toggle="modal" data-target=".bd-example-modal-lg">Register child</button>
                    </a>
                </div>
            </div>
            </div>
        </section>
        <style jxs>{`
            section {
                margin-top: 50px;
                padding: 60px;
                background-color: #EEBA37;
            }
            
            section h3 {
                color: white;
                font-size: 20px;
                text-transform: uppercase;
                letter-spacing: 0.06em;
                margin-bottom: 20px;
            }
            
            section h1 {
                font-size: 33px;
            }
            
            section button {
                width: 200px;
                color: #EEBA37;
                background-color: white;
                border: 0px;
                border-radius: 7px;
                padding: 10px;
                font-size: 20px;
            }
        `}</style>
    </div>
);

export default Reminder;